.ant-carousel {
    width: 100% !important
}
.carousel-user {
    width: 100% !important;
    .slick-dots {
        li {
            width: auto !important;
            margin: 0 4px;
            button {
                width: 8px;
                height: 8px;
                background: var(--color-primary-500);
                border-radius: 50%
            }
        }
        .slick-active {
            button {
                background: var(--color-primary-500) !important;
            }
        }
    }
}